exports.addTodo = addTodo;
var deleteTodo = redux_actions_1.createAction(types.DELETE_TODO, function (todo) { return todo; });
exports.deleteTodo = deleteTodo;
var editTodo = redux_actions_1.createAction(types.EDIT_TODO, function (todo, newText) { return lodash_1.assign(todo, { text: newText }); });
exports.editTodo = editTodo;
var completeTodo = redux_actions_1.createAction(types.COMPLETE_TODO, function (todo) { return todo; });
exports.completeTodo = completeTodo;
var completeAll = redux_actions_1.createAction(types.COMPLETE_ALL, function () { });
exports.completeAll = completeAll;
var clearCompleted = redux_actions_1.createAction(types.CLEAR_COMPLETED, function () { });
exports.clearCompleted = clearCompleted;



